@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&display=swap');
*{
    margin: 0;
    padding:0 ;
    box-sizing: border-box;
    font-family: "Dancing Script", cursive;

}
body{
    background: #f5f5f5;
}
a{
    text-decoration: none;
    cursor: pointer;

}
.container{
    padding-left: 10%;
    padding-right: 10%;
}
.btn{
    background:#fff;
    color:#130b0b;
    padding:14px 28px ;
    font-size: 16px;
    border-radius: 25px;
    cursor: pointer;
    border: 0;
    outline: 0;
}
.btn:hover{
    
   background: #dfe4b2; 
    
}
.btn.dark-btn{
    background: #212ea0;
    color: #fff;
}


/*media*/
@media (max-width:1200){
    .container{
        padding-left: 5%;
        padding-right: 5%;
    }
    .btn{
        padding: 14px 22px;
    }
}