nav{
   width: 100%;
   /*background-color: #AAB7B8  ;*/
   color: #acbcd1;
   padding: 4px 0;
   position: fixed;
   top: 0;
   left: 0;
   display: flex;
   align-items: center;
   justify-content: space-between;
   z-index: 10;
}
.logo{
    width: 3.5rem;
    background-color: aliceblue;
    border-radius: 5px  25px 0px ;
   
   
    font-size: 20px;
}
nav ul li{
    display: inline-block;
    list-style: none;
    margin: 5px 20px ;
    font-size: 20px;
}
nav ul li:hover{
    cursor: pointer;
    color: rgb(249, 145, 75);
}
.dart-nav{
    background: #212ea0;
    transition: 0.5s;
    
}
.menu-icon{
    display: none;
}


/* media quary*/
@media (max-width:1000){
    .logo{
        width: 140px;
    }
    nav ul li{
        margin: 10px 15px;
    }
}
@media (max-width:895px){
    nav{
        padding: 15px 0;
    }
    nav ul{
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0px;
        background:#212ea0 ;
        z-index: -1;
        width: 200px;
        padding-top: 70px;
        transition: 0.5s;
    }
    nav ul li{
        display: block;
        margin: 25px 40px;
    }
    .menu-icon{
        display: block;
        width: 30px;
        cursor: pointer;
        filter: invert(100%);
    }
    .hide-mobile-menu{
     right: -200px;
    }
}